import React,{useEffect}   from "react";
import './App.css';
import Header from './Header';
import Home from './Home';
import { BrowserRouter as Router,Switch,Route } from "react-router-dom";
import Checkout from './Checkout'
import Login from './Login'
import Payment from './Payment';
import {auth} from './firebase';
import {useStateValue} from "./StateProvider" ;
import{loadStripe} from "@stripe/stripe-js";
import {Elements} from "@stripe/react-stripe-js";


const promise =loadStripe('pk_test_51MIIkuSDaFeSrTp9mwhbmraYlY7V9qOIPU5GObUUWpOSdiXC1bUCFhesU98fYpsShuUuwAUcvvju1xXiXSECOzt200D99yokRu');

function App() { 
  const[{},dispatch] = useStateValue();
        useEffect(()=>{

                 // this code will onl runs when the app component loads.....
                 auth.onAuthStateChanged(authUser=>{
                  console.log('the user is >>>  ' ,authUser);
                  if(authUser){
                    // user was logged in /the user just logged in 
                    dispatch({
                      type:'SET_USER',
                      user:authUser
                    })
                  }else{
                    // user is logged out
                    
                    dispatch({
                      type:'SET_USER',
                      user:null
                    })  
                  }
                 })
        },[])
  return (
    //BEM CONVENTION
    <Router>
      <div className="app">
                      
        <Switch>
            <Route path="/login">
             <Login/> 
            
           </Route>
           <Route path="/checkout">
              <Header/>
              <Checkout/>
           </Route>
           
           <Route path="/payment">
             <Header/>
             <Elements  stripe={promise} > 
             <Payment/>
             </Elements>
           </Route> 

           <Route path="/">
             <Header/>
             <Home/>
           </Route> 
        </Switch>
    </div>
    </Router>
  );
}
  
export default App;
